import getGridConfig from '../services/GridConfig';
/**
 * Get the current used grid config
 */

export default {
  data: function data() {
    return {
      gridConfig: {}
    };
  },
  created: function created() {
    var _this = this;

    getGridConfig.$on('changed', function (val) {
      _this.gridConfig = val;
    });
    console.debug("[".concat(appName, "]"), 'Grid config', Object.assign({}, getGridConfig.gridConfig));
    this.gridConfig = getGridConfig.gridConfig;
  },
  beforeDestroy: function beforeDestroy() {
    getGridConfig.$off('changed', this.gridConfig);
  }
};